@import url('https://fonts.googleapis.com/css?family=Merriweather');

body, html, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #f9ca24;
  scroll-behavior: smooth;
}

* {
  font-family: 'Merriweather', sans-serif;
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h1 {
  letter-spacing: -.036em;
  font-weight: 400;
  font-size: 42px;
}

p {
  font-size: 22px;
  line-height: 1.6363636364;
  margin-bottom: 30px;
  font-weight: 300;
  opacity: .8;
}

a {
  color: black;
}
.app {
  height: 100%;
  width: 100%;
  margin: auto;
}

.header {
  top: 0; /* Position the navbar at the top of the page */
  margin: auto;
  width: 300px;
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr;

}

.header a {
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  font-size: 18px;
  font-size: 22px;
  line-height: 1.6363636364;
  font-weight: 300;
  color: 202020;
}

.slide {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.jumbotron {
  margin-top: -64px;
  max-width: 900px;
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-template-columns: 1fr 1fr;
}

.lightBulb {
  height: auto; 
  width: 85%; 
  min-width: 200px;
  max-width: 500px; 
  max-height: 500px;
  grid-column: 2;
}

@media only screen and (max-width: 800px) {
  .jumbotron{
    margin-left: 20px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-auto-flow: dense;
    margin-bottom: 50px;
  }
  .lightBulb {
    width: 75%; 
    grid-column: 1;
    grid-row: 1;
    -ms-flex-item-align: end;
        align-self: flex-end;
  }

  .jumbotronText {
    -ms-flex-item-align: start;
        align-self: flex-start;
  }
}

.past {
  max-width: 900px;
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 40px;
}

.study {
  height: auto; 
  width: auto; 
  max-width: 450px; 
  max-height: 450px;
}

@media only screen and (max-width: 800px) {
  .past {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 50px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-auto-flow: dense;
  }
  .study {
    width: 85%; 
    grid-column: 1;
    grid-row: 1;
    -ms-flex-item-align: end;
        align-self: flex-end;
  }
}

.present {
  max-width: 900px;
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 40px;
}

.hands {
  height: auto; 
  width: auto; 
  max-width: 450px; 
  max-height: 450px;
}

@media only screen and (max-width: 800px) {
  #present {
    margin-top: 400px;
  }
  .present {
    margin-left: 10px;
    margin-right: 10px;
    padding-bottom: 500px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-auto-flow: dense;
  }
  .hands {
    width: 85%; 
    grid-column: 1;
    grid-row: 1;
    -ms-flex-item-align: end;
        align-self: flex-end;
  }
}

.future {
  max-width: 900px;
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 40px;
}

.future a {
  color: black;
  padding-right: 30px;
}

.contactMe {
  height: auto; 
  width: auto; 
  max-width: 500px; 
  max-height: 500px;
}

@media only screen and (max-width: 800px) {
  #future {
    margin-top: 400px;
  }
  .future {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 30px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-auto-flow: dense;
    grid-gap: 0px;
  }
  .contactMe {
    width: 70%; 
    grid-column: 1;
    grid-row: 1;
    -ms-flex-item-align: end;
        align-self: flex-end;
  }
}




